import React from 'react';
import CardParagraph from './CardParagraph';
import { useIntl } from "react-intl"

const AdvantagesInformation = ({ data }) => {
    const { formatMessage: f } = useIntl()

    const informationAdvantagesLefts = data.advantageData ? data.advantageData.filter(obj => obj.advantageId % 2) : []
    const informationAdvantagesRights = data.advantageData ? data.advantageData.filter(obj => obj.advantageId % 2 === 0) : []

    const text = 'ng ln - 2 Mt tin - Gn Metro - Gn ch - Gn cng vin - Gn Sn bay - Gn Bus - Gn Nh G - Mt tin kinh doanh - Lm Vn Phng'

    return <CardParagraph title={f({ id: "advantageData" })} text={text} />
}

export default AdvantagesInformation