import React from "react";
import CardMultiLabels from "./CardMultiLabels";
import { useIntl } from "react-intl"

const HouseInformation = (props) => {
  const { data } = props;
  const { formatMessage: f } = useIntl()

  const leftData = [
    {
      name: "tng",
      value: data.realEstateHouseFloors,
      unit: "tng"
    },
    {
      name: "dt sn",
      value: data.realEstateHouseFloorsSquare,
      unit: "m",
    },
    {
      name: "pn",
      value: data.realEstateHouseBedRooms,
      unit: "PN"
    },
    {
      name: "wc",
      value: data.realEstateHouseToilets,
      unit: "WC"
    },
  ];
  const rightData = [
    {
      name: "hon thin",
      value: null
    },
    {
      name: "tnh trng",
      value: null
    },
    {
      name: "hng",
      value: data.realEstateHouseDirection
    }
  ];

  return <CardMultiLabels title={f({ id: "houseInfo" })} leftData={leftData} rightData={rightData} />
};

export default HouseInformation;