import React from 'react';
import CardParagraph from './CardParagraph';
import { useIntl } from "react-intl"

const WeaknessInformation = ({ data }) => {
    const { formatMessage: f } = useIntl()

    const informationWeaknessLefts = data.disadvantageData ? data.disadvantageData.filter(obj => obj.advantageId % 2) : []
    const informationWeaknessRights = data.disadvantageData ? data.disadvantageData.filter(obj => obj.advantageId % 2 === 0) : []

    const cardData = [
        'Gn Ng 3',
        'Gn Cha',
        'Gn Nh Th',
        'Gn M',
        'C Cy to trc nh',
        'Cng trc nh',
        'ng hm nh',
        'S xt hng chc',
        'Hm ct',
        'N hp'
    ]

    const text = cardData.join(' - ')

    return <CardParagraph title={f({ id: "disadvantageData" })} text={text} />
}

export default WeaknessInformation