import React from "react";
import CardMultiLabels from "./CardMultiLabels";
import { useIntl } from "react-intl"

const Juridicalsinformation = (props) => {
  const { data } = props;
  const { formatMessage: f } = useIntl()

  const leftData = [
    {
      name: "gcn",
      value: null
    },
    {
      name: "mc ch",
      value: null
    },
    {
      name: "th chp",
      value: null
    },
    {
      name: "gii ta",
      value: null
    }
  ]

  const rightData = [
    {
      name: "tranh chp",
      value: null
    },
    {
      name: "quy hoch",
      value: data.agencyPercent
    },
    {
      name: "hin trng",
      value: null
    },
  ]

  return <CardMultiLabels title={f({ id: "juridical" })} leftData={leftData} rightData={rightData} />
};

export default Juridicalsinformation;